<template>
  <div>
    <vs-card class="p-5">
      <a href="javascript:void()" @click="$router.go(-1)">← Back</a>
      <h4 class="inline-block ml-2">Create Church Account</h4>
      <div class="contained-example-container">
        <form class="mt-4 vs-con-loading__container" id="div-with-loading">
          <div class="vx-row">
            <div class="vx-col w-1/2 pt-3">
              <vs-input
                label-placeholder="Church Name"
                v-model="church.Name"
                name="input1"
                class="w-full mt-5"
              />
            </div>
            <div class="vx-col w-1/2 pt-3">
              <label>Country</label>
              <v-select
                @input="changeCountry"
                class="mt-1"
                v-model="church.countryId"
                label="name"
                :options="countries"
              />
            </div>
            <div class="vx-col w-1/2 pt-3">
              <label>State</label>
              <v-select class="mt-1" v-model="church.State" label="name" :options="states" />
            </div>
            <div class="vx-col w-1/2 pt-3">
              <vs-input
                label-placeholder="Region"
                v-model="church.Region"
                name="input1"
                class="w-full mt-5"
              />
            </div>
            <div class="vx-col w-1/2 pt-3">
              <vs-input
                label-placeholder="Country Code"
                v-model="church.CountryCode"
                name="input1"
                class="w-full mt-5"
              />
            </div>

            <div class="vx-col w-1/2 pt-3">
              <vs-input
                label-placeholder="Phone Number"
                v-model="church.PhoneNumber"
                name="input1"
                class="w-full mt-5"
              />
            </div>
            <div class="vx-col w-1/2 pt-3">
              <vs-input
                label-placeholder="Email"
                v-model="church.Email"
                name="input1"
                class="w-full mt-5"
              />
            </div>
            <div class="vx-col w-1/2 pt-3">
              <vs-input
                label-placeholder="Address"
                v-model="church.Address"
                name="input1"
                class="w-full mt-5"
              />
            </div>
            <div class="vx-col w-1/2 pt-3">
              <vs-input
                label-placeholder="Zone"
                v-model="church.Zone"
                name="input1"
                class="w-full mt-5"
              />
            </div>
            <div class="vx-col w-1/2 pt-3">
              <vs-input
                label-placeholder="Pastor"
                v-model="church.Pastor"
                name="input1"
                class="w-full mt-5"
              />
            </div>

            <div class="vx-col w-1/2 pt-3">
              <vs-input
                label-placeholder="Transaction Cost"
                v-model="church.transCost"
                name="input1"
                class="w-full mt-5"
              />
            </div>

            <div class="vx-col w-1/2 pt-3">
              <vs-input
                label="Logo"
                accept="image/png, image/jpeg, image/jpg"
                @change="handleFileChange($event)"
                name="input1"
                type="file"
                class="w-full mt-0"
              />
            </div>
          </div>
          <vs-button type="filled" @click.prevent="submitForm" class="mt-5 block">Create Account</vs-button>
        </form>
      </div>
    </vs-card>
  </div>
</template>

<script>
import vSelect from "vue-select";
export default {
  components: {
    "v-select": vSelect
  },
  data() {
    return {
      countries: [],
      states: [],
      church: {
        Name: "",
        State: "",
        Region: "",
        CountryCode: "",
        PhoneNumber: "",
        Email: "",
        countryId: "",
        Address: "",
        Logo: "",
        Zone: "",
        Pastor: "",
        transCost: ""
      }
    };
  },
  mounted() {
    this.openLoadingInDiv();
    this.$store
      .dispatch("getCountries")
      .then(resp => {
        this.countries = resp.data;
        this.$vs.loading.close("#div-with-loading > .con-vs-loading");
      })
      .catch(err => {
        this.$vs.loading.close("#div-with-loading > .con-vs-loading");
      });
  },
  methods: {
    openLoadingInDiv() {
      this.$vs.loading({
        container: "#div-with-loading",
        scale: 0.6
      });
    },
    handleFileChange: function(event) {
      console.log(event.target.files[0]);
      this.church.Logo = event.target.files[0];
    },
    changeCountry(value) {
      this.openLoadingInDiv();
      let countryId = value._id;
      this.$store
        .dispatch("getState", countryId)
        .then(resp => {
          this.states = resp.data;
          this.$vs.loading.close("#div-with-loading > .con-vs-loading");
        })
        .catch(err => {
          this.$vs.loading.close("#div-with-loading > .con-vs-loading");
        });
    },
    submitForm() {
      // this.church.State = this.church.State.name;
      // this.church.countryId = this.church.countryId._id;
      this.openLoadingInDiv();
      let church = this.church;
      this.$store
        .dispatch("createChurch", church)
        .then(resp => {
          this.$vs.loading.close("#div-with-loading > .con-vs-loading");
          this.$vs.notify({
            title: "Create Church Account",
            text: "Church Account created successfully!",
            color: "success"
          });
          this.church = {
            Name: "",
            State: "",
            Region: "",
            CountryCode: "",
            PhoneNumber: "",
            Email: "",
            countryId: "",
            Address: "",
            Logo: "",
            Zone: "",
            Pastor: "",
            transCost: ""
          };
          console.log(resp);
        })
        .catch(err => {
          this.$vs.loading.close("#div-with-loading > .con-vs-loading");

          if (err.response) {
            if (err.response.status == 400) {
              console.log(err.response.data);
              this.$vs.notify({
                title: "Create Church Account",
                text: err.response.data,
                color: "warning"
              });
            } else {
              this.$vs.notify({
                title: "Create Church Account",
                text: "Unable to create Church account, try again!",
                color: "warning"
              });
            }
          } else {
            this.$vs.notify({
              title: "Create Church Account",
              text: "Unable to create Church account, try again!",
              color: "warning"
            });
          }
        });
    }
  }
};
</script>
